/* twitter Factory */

.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}

.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
}

.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
}

.factoryInput__label {
    color: #04aaff;
    cursor: pointer;
}

.factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm__attachment img {
    height: 80px;
    width: 80px;
    border-radius: 40px;
}

.factoryForm__clear {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
}

.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}

/* tweet */

.tweetEdit .formBtn {
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 5px;
}

.tweet {
    margin-bottom: 20px;
    background-color: white;
    width: 100%;
    max-width: 320px;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
}

.tweet h4 {
    font-size: 14px;
}

.tweet img {
    right: -10px;
    top: 20px;
    position: absolute;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-top: 10px;
}

.tweet__actions {
    position: absolute;
    right: 10px;
    top: 10px;
}

.tweet__actions span {
    cursor: pointer;
}

.tweet__actions span:first-child {
    margin-right: 10px;
}